import {Component} from '@angular/core';

import {IonicPage, ModalController, Platform} from 'ionic-angular';
import {ModalGive} from "./modal-give.component";
import {InAppBrowser} from '@ionic-native/in-app-browser';


@IonicPage()
@Component({
  selector: 'give-page',
  templateUrl: 'template.html'
})
export class GivePage {
  home: string = "online";
  isAndroid: boolean = false;

  constructor(platform: Platform,
              public modalCtrl: ModalController,
              private iab: InAppBrowser,
              ) {
    this.isAndroid = platform.is('android');


  }
  openPaynow(){
    const browser = this.iab.create('https://www.paynow.co.zw/Payment/Link/?q=c2VhcmNoPWJvcnJvd2RhbGVjb21tdW5pdHljaHVyY2glNDBnbWFpbC5jb20mYW1vdW50PTEuMDAmcmVmZXJlbmNlPSZsPTA%3d','_blank');
browser.show()
  }

  openDetails(){
    const browser = this.iab.create(
      'https://docs.google.com/gview?embedded=true&url=https://firebasestorage.googleapis.com/v0/b/dship-6fc02.app' +
      'spot.com/o/Giving%20Details%20Banner.pdf?alt=media&token=5f356ed8-33ab-4e1d-a6c2-5d9a14c26807','_blank');
    browser.show()
  }

  openGive(model) {

    let modal = this.modalCtrl.create(ModalGive, model);
    modal.present();
  }
}



